.zoomWrapper {
	position: absolute;
	top:0;
	left:0;
	bottom:0;
	right:0;
	overflow: hidden;
}
.Gallery {
	width: 100vw;
	height: 100vh;
	overflow: hidden;
	font-family: 'Nunito', sans-serif;
	position: relative;
	display: grid;
	grid-template-columns: repeat(2, .5fr) repeat(11, 1fr);
	grid-template-rows: repeat(10, 1fr) repeat(2, .5fr);
	background-color: #C8A55F;
	-webkit-transition: -webkit-transform .5s ease;
	transition: -webkit-transform .5s ease;
	-o-transition: transform .5s ease;
	transition: transform .5s ease;
	transition: transform .5s ease, -webkit-transform .5s ease;
}
.Gallery__logo {
	grid-column: 3 / -1;
	grid-row: 1 / 2;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	font-family:'Krona One';
	letter-spacing: .1rem;
	font-size: 2vh;
	margin-left: -1rem;
}
.Gallery__title {
	grid-column: 1 / 2;
	grid-row: 2 / -1;
	letter-spacing: .5rem;
	font-size: .7rem;
	color: black;
	text-align: left;
	border-right: 1px solid black;
}
.Gallery__title p {
	display: block;
	position: absolute;
	font-size: 1.2rem;
	-webkit-transform:  rotate(-90deg);
	    -ms-transform:  rotate(-90deg);
	        transform:  rotate(-90deg);
	left: calc(4vw - 1.2rem);
	bottom: 20%;
	-webkit-transform-origin: left top;
	    -ms-transform-origin: left top;
	        transform-origin: left top;
}
.Gallery__projects--wrapper {
	grid-column: 3 / 8;
	grid-row: 2 / 6;
	overflow: hidden;
	position: relative;
	right: 4.25%;
}
.Gallery__projects--wrapperForItems {
	position: absolute;
	top: 10vh;
	width: 100%;
	height: 12vh;
}
.Gallery__projects--wrapper svg {
	position: absolute;
	width: 4vh;
	height: 4vh;
	top: 3.8vh;
	right: -2vh;
	fill: rgba(0,0,0,.8);
	-webkit-transform: rotate(0);
	    -ms-transform: rotate(0);
	        transform: rotate(0);
	-webkit-transition: all .5s ease;
	-o-transition: all .5s ease;
	transition: all .5s ease;
}
.Gallery__projects--wrapper svg.goUp {
	-webkit-transform: rotate(90deg);
	    -ms-transform: rotate(90deg);
	        transform: rotate(90deg);
}
.Gallery__projects--wrapper svg.goDown {
	-webkit-transform: rotate(-90deg);
	    -ms-transform: rotate(-90deg);
	        transform: rotate(-90deg);	
}

.Gallery__projects--item {
	position: absolute;
	top: 4vh;
	right: 8vh;
	height: 4vh;
	text-align: right;
	-webkit-transform: translateY(-180%) scale(.6);
	    -ms-transform: translateY(-180%) scale(.6);
	        transform: translateY(-180%) scale(.6);
	z-index: 100;
	opacity: 0;
	font-size: 4vh;
	line-height: 4vh;
	font-family: 'Krona One';
	color: rgba(0,0,0,1);
	-webkit-transition: all .5s ease;
	-o-transition: all .5s ease;
	transition: all .5s ease;
}
.Gallery__projects--item.top {
	-webkit-transform: translateY(-150%) scale(.9);
	    -ms-transform: translateY(-150%) scale(.9);
	        transform: translateY(-150%) scale(.9);
	opacity: .3;
}
.Gallery__projects--item.center {
	-webkit-transform: translateY(0) scale(1.1);
	    -ms-transform: translateY(0) scale(1.1);
	        transform: translateY(0) scale(1.1);
	opacity: 1;
}
.Gallery__projects--item.bottom {
	-webkit-transform: translateY(150%) scale(.9);
	    -ms-transform: translateY(150%) scale(.9);
	        transform: translateY(150%) scale(.9);
	opacity: .3;
}
.Gallery__projects--item.under {
	-webkit-transform: translateY(180%) scale(.6);
	    -ms-transform: translateY(180%) scale(.6);
	        transform: translateY(180%) scale(.6);
	opacity: 0;
}



.Gallery__projects--description {
	grid-column: 2 / 8;
	grid-row: 7 / 11;
	font-size: 1.0rem;
	line-height: 1.4rem;
	color: rgba(0,0,0,.8);
	text-align: right;
	position: relative;
}

.Gallery__projects--description div {
	position: absolute;
	top: 0;
	left:15%;
	right: 10%;
	bottom: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	opacity: 0;
	-webkit-transition: all .4s ease-in-out;
	-o-transition: all .4s ease-in-out;
	transition: all .4s ease-in-out;
}
.Gallery__projects--description div p {
	border-right: 1px solid rgba(54, 55, 62, 1);
	padding-right: 20px;
	line-height: 1.8rem;

}

.Gallery__projects--description div.showDescription {
	opacity: 1; 
}


.toTheGallery {
	grid-column: 6 / 9;
	grid-row: 6 / 7;
	height: 50%;
	width: 60%;
	background:-webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,.5)), color-stop(50%, rgba(0,0,0,.8)), to(rgba(0,0,0,.5)));
	background:-webkit-linear-gradient(top, rgba(0,0,0,.5), rgba(0,0,0,.8) 50%, rgba(0,0,0,.5));
	background:-o-linear-gradient(top, rgba(0,0,0,.5), rgba(0,0,0,.8) 50%, rgba(0,0,0,.5));
	background:linear-gradient(180deg, rgba(0,0,0,.5), rgba(0,0,0,.8) 50%, rgba(0,0,0,.5));
	color: white;
	position: relative;
	right: 4.25%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	cursor: pointer;
	z-index: 100;
	-webkit-transition: all 1s ease;
	-o-transition: all 1s ease;
	transition: all 1s ease;
	opacity: 0;

}
.toTheGallery.showGalleryButton {
	-webkit-animation: showGalleryButton 1s 2s ease forwards;
	        animation: showGalleryButton 1s 2s ease forwards;
}
@-webkit-keyframes showGalleryButton {
	0%{
		opacity: 0;
		-webkit-transform: translateX(-20px);
		        transform: translateX(-20px);
	}
	100% {
		opacity: 1;
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
}
@keyframes showGalleryButton {
	0%{
		opacity: 0;
		-webkit-transform: translateX(-20px);
		        transform: translateX(-20px);
	}
	100% {
		opacity: 1;
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
}
.toTheGallery a {
	display: block;
	width: 80%;
	height: 100%;
	color: white;
	text-decoration: none;
	font-size: 2vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}
.toTheGallery.showGalleryButton:hover {
	background-color: black;
}
.toTheGallery svg {
	fill: white;
	margin-left: 5px;
	width: 1rem;
	height: 1rem;
}



.Gallery__posters {
	grid-column: 8 / -1;
	grid-row: 1 / -1;
	overflow: auto;
	overflow: hidden;
	-webkit-perspective: 1px;
	        perspective: 1px;
	-webkit-perspective-origin: 0 0;
	        perspective-origin: 0 0;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
}

.Gallery__posters img {
	width: auto;
	height: auto;
	position: absolute;
	-webkit-transform-origin: 0 0;
	    -ms-transform-origin: 0 0;
	        transform-origin: 0 0;
}

.Gallery__posters .img1 {
	left: 0;
	top: 20%;
	-webkit-transform: translateZ(-2px) scale(2);
	        transform: translateZ(-2px) scale(2);
	opacity: 1;
	-webkit-transition: all .8s cubic-bezier(.5,0,.5,1);
	-o-transition: all .8s cubic-bezier(.5,0,.5,1);
	transition: all .8s cubic-bezier(.5,0,.5,1);
}
.Gallery__posters .img2 {
	left: 100%;
	top: 200%;
	-webkit-transform: translateZ(-3px) scale(2.4);
	        transform: translateZ(-3px) scale(2.4);
	opacity: 1;
	-webkit-transition: all 1s cubic-bezier(.5,0,.5,1);
	-o-transition: all 1s cubic-bezier(.5,0,.5,1);
	transition: all 1s cubic-bezier(.5,0,.5,1);
}
.Gallery__posters .img3 {
	left: 100%;
	top: 5%;
	-webkit-transform: translateZ(-1px) scale(1.5);
	        transform: translateZ(-1px) scale(1.5);
	opacity: 1;
	-webkit-transition: all 1.1s cubic-bezier(.5,0,.5,1);
	-o-transition: all 1.1s cubic-bezier(.5,0,.5,1);
	transition: all 1.1s cubic-bezier(.5,0,.5,1);
}


.Gallery__posters .img1.hide-top {
	-webkit-transform: translateZ(-2px) translateY(-350%) scale(2);
	        transform: translateZ(-2px) translateY(-350%) scale(2);
	opacity: .5;
} 
.Gallery__posters .img2.hide-top {
	-webkit-transform: translateZ(-3px) translateY(-600%) scale(2.4);
	        transform: translateZ(-3px) translateY(-600%) scale(2.4);
	opacity: .5;
} 
.Gallery__posters .img3.hide-top  {
	-webkit-transform: translateZ(-1px) translateY(-350%) scale(1.5);
	        transform: translateZ(-1px) translateY(-350%) scale(1.5);
	opacity: .5;
}


.Gallery__posters .img1.hide-bottom {
	-webkit-transform: translateZ(-2px) translateY(500%) scale(2);
	        transform: translateZ(-2px) translateY(500%) scale(2);
	opacity: .5;
} 
.Gallery__posters .img2.hide-bottom {
	-webkit-transform: translateZ(-3px) translateY(450%) scale(2.4);
	        transform: translateZ(-3px) translateY(450%) scale(2.4);
	opacity: .5;
} 
.Gallery__posters .img3.hide-bottom  {
	-webkit-transform: translateZ(-1px) translateY(400%) scale(1.5);
	        transform: translateZ(-1px) translateY(400%) scale(1.5);
	opacity: .5;
}

@-webkit-keyframes description {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes description {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}



