.About {
	position: absolute;
	width: 100vw;
	height: 100vh;
	-webkit-transform: translateY(-100%);
	    -ms-transform: translateY(-100%);
	        transform: translateY(-100%);
	-webkit-transition: -webkit-transform 1s ease;
	transition: -webkit-transform 1s ease;
	-o-transition: transform 1s ease;
	transition: transform 1s ease;
	transition: transform 1s ease, -webkit-transform 1s ease;
	background-color: gray;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.show {
	-webkit-transform: translateY(0%);
	    -ms-transform: translateY(0%);
	        transform: translateY(0%);
}

.About__container{
	width: 85vw;
	height: 80%;
	overflow: hidden;
	background-color: white;
	display: grid;
	grid-template-columns: repeat(10, 1fr);
	grid-template-rows: repeat(7, 1fr);

}

.About__container--logo {
	grid-column: 6 / -1;
	grid-row: 1 / 3;
	padding: 1rem;
	font-family: 'Russo One', sans-serif;
	font-size: 8vh;
	color: #252526;
}

.About__container--photo {
	grid-column: 1 / 6;
	grid-row: 1 / 6;
	background-color: black;
	position: relative;
	background: url(../img/about_img/img1.jpg) right top/cover no-repeat;
}

.About__container--info {
	grid-column: 6 / -1;
	grid-row: 3 / -1;
	background-color: #2D2D2D;
	color: white;
	overflow: hidden;
	font-size: .8rem;
	padding: 1rem;
	font-family: 'Merriweather Sans';
	position: relative;
}

.tempLayout {
	position: absolute;
	width: 100%;
	height: 100%;
	-webkit-animation: move-to-left 1.2s .35s ease forwards;
	        animation: move-to-left 1.2s .35s ease forwards;
	background-color: white;
}
.About__container--info .tempLayout{
	top: -1rem;
	left: -1rem;
	width: calc(100% + 1rem);
	height: calc(100% + 1rem);
	-webkit-animation: move-to-right 1s .3s ease forwards;
	        animation: move-to-right 1s .3s ease forwards;
} 

@-webkit-keyframes move-to-left{
	0%{
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
	100% {
		-webkit-transform: translateX(-100%);
		        transform: translateX(-100%);
	}
} 

@keyframes move-to-left{
	0%{
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
	100% {
		-webkit-transform: translateX(-100%);
		        transform: translateX(-100%);
	}
}
@-webkit-keyframes move-to-right{
	0%{
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
	100% {
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
	}
}
@keyframes move-to-right{
	0%{
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
	100% {
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
	}
}


