.App {
	position: relative;
	width: 100vw;
	height: 100vh;
	overflow: hidden;
}


/*Common class for all containers with in and out animations*/
.MainWrapper {
	position: relative;
	width: 100%;
	height: 100%;
	
}
.MainWrapper.on-exit{
	-webkit-animation: exit-animation .8s ease forwards;
	        animation: exit-animation .8s ease forwards;
}
.MainWrapper.on-enter{
	-webkit-transform: scale(1.1);
	    -ms-transform: scale(1.1);
	        transform: scale(1.1);
	-webkit-animation: enter-animation 2s ease forwards;
	        animation: enter-animation 2s ease forwards;
}
@-webkit-keyframes enter-animation {
	0%{
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
@keyframes enter-animation {
	0%{
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
@-webkit-keyframes exit-animation {
	0%{
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	100% {
		opacity: 0;
		-webkit-transform: scale(1.3);
		        transform: scale(1.3);
	}
}
@keyframes exit-animation {
	0%{
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	100% {
		opacity: 0;
		-webkit-transform: scale(1.3);
		        transform: scale(1.3);
	}
}







